html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   height: 100%;
   overflow: hidden;
   background: #FFE259;
   background: var(--mango-gradiant);
}

:root {
  --primary-gradient: linear-gradient(to right, #4E4376, #2B5876);
  --mango-gradiant: linear-gradient(to right, #FFA751, #FFE259);
  --text-color: #F7F9F9;
  --Montserrat: "Montserrat", cursive;
  --Lobster: "Lobster", cursive;
  --primary-shadow: 3px 4px 7px rgba(27, 27, 27, .534);
  --border-color: rgba(0, 0, 0, .329);
}